// frontend/pages/messages/index.js
import { useEffect, useState, useRef } from "react";
import api from "../../utils/api";
import { io } from "socket.io-client";
import { useRouter } from "next/router";

const socket = io("http://localhost:5000");

export default function Messages() {
  const router = useRouter();
  const { post } = router.query; // query ?post=ID

  const [messages, setMessages] = useState([]);
  const [text, setText] = useState("");
  const [userId, setUserId] = useState(null);
  const bottomRef = useRef(null);

  const getSharedPosts = () => {
    if (typeof window === "undefined") return [];
    const saved = localStorage.getItem("sharedPosts");
    return saved ? JSON.parse(saved) : [];
  };

  const addSharedPost = (postId) => {
    const shared = getSharedPosts();
    if (!shared.includes(postId)) {
      shared.push(postId);
      localStorage.setItem("sharedPosts", JSON.stringify(shared));
    }
  };

  const isAlreadyShared = (postId) => getSharedPosts().includes(postId);

  // Load user ID
  useEffect(() => {
    const id = localStorage.getItem("userId");
    if (!id) {
      router.push("/login");
      return;
    }
    setUserId(id);
    socket.emit("join_room", id);
  }, []);

  // Auto scroll
  useEffect(() => {
    bottomRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  // Socket receive
  useEffect(() => {
    socket.on("new_message", (msg) => {
      if (String(msg.userId) === String(userId)) {
        setMessages((prev) => [...prev, msg]);
      }
    });
    return () => socket.off("new_message");
  }, [userId]);

  // Load all messages
  useEffect(() => {
    if (userId) loadMessages();
  }, [userId]);

  const loadMessages = async () => {
    try {
      const res = await api.get(`/messages/${userId}`);
      setMessages(res.data);
    } catch (err) {
      console.error(err);
    }
  };

  // ðŸ”¥ Handle post share once
  useEffect(() => {
    if (!router.isReady || !userId || !post) return;
    if (isAlreadyShared(post)) return; // duplicate block

    const sharePost = async () => {
      try {
        const res = await api.get(`/posts/${post}`); // load post info
        const msg = await api.post("/messages/send", {
          userId,
          text: `Shared a post: ${res.data.title} (${res.data.price} BDT)`,
          type: "shared_post",
          postId: res.data._id
        });

        setMessages((prev) => [...prev, msg.data]);
        socket.emit("send_message", msg.data);

        addSharedPost(post); // mark as shared
      } catch (err) {
        console.error("Post sharing failed:", err);
      }
    };

    sharePost();
  }, [router.isReady, userId, post]);

  // Send text message
  const sendMessage = () => {
    if (!text.trim() || !userId) return;

    api.post("/messages/send", { userId, text, type: "text" })
      .then((msgRes) => {
        setMessages((prev) => [...prev, msgRes.data]);
        socket.emit("send_message", msgRes.data);
      });

    setText("");
  };

  return (
    <div className="h-screen flex flex-col bg-gray-100">
      <div className="bg-blue-600 text-white p-4 flex items-center justify-between">
        <h2 className="text-xl font-semibold">Chat with Admin</h2>
        <a href="/" className="bg-white text-blue-700 px-3 py-1 rounded-lg shadow font-semibold">
          Home
        </a>
      </div>

      <div className="flex-1 overflow-y-auto p-4 space-y-3 bg-gradient-to-b from-gray-200 to-gray-100">
        {messages.map((m) => (
          <div key={m._id} className={m.sender === "user" ? "text-right" : "text-left"}>
            {m.type === "shared_post" ? (
              <div
                onClick={() => router.push(`/posts/${m.postId}`)}
                className="inline-block bg-gray-200 text-black px-3 py-2 rounded-lg cursor-pointer hover:bg-gray-300"
              >
                {m.text}
              </div>
            ) : (
              <div className={`inline-block px-4 py-2 rounded-xl shadow-md ${
                  m.sender === "user"
                    ? "bg-blue-500 text-white"
                    : "bg-white text-gray-900 border"
                }`}
              >
                {m.text}
              </div>
            )}
          </div>
        ))}
        <div ref={bottomRef}></div>
      </div>

      <div className="p-3 border-t bg-white flex items-center gap-2">
        <input
          className="flex-1 border p-3 rounded-xl bg-gray-100"
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="Type your message..."
        />
        <button
          onClick={sendMessage}
          className="bg-blue-600 text-white px-5 py-3 rounded-xl shadow hover:bg-blue-700"
        >
          Send
        </button>
      </div>
    </div>
  );
}
